body {
    font-family: 'Gilroy';
    font-size: 15px;
    min-width: 320px;
    letter-spacing: 0.5px;
    color: $text;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0 0 30px 0;
    padding: 0;
}

ol,
ul {
    padding-left: 20px;
}

a {
    text-decoration: none;

    &:hover {
        text-decoration: none;
    }
}

input {
    &:focus {
        outline: 0;
    }
}

button {
    &:focus {
        outline: 0;
    }
}

.container {
    padding-left: $fields;
    padding-right: $fields;
}

.main-page {
    margin: 100px 0;
}